%background-image-contain {
  background-size: contain;
  background-position: top center;
  background-repeat: no-repeat;
}

%background-image-cover {
  background-size: cover;
  background-position: top center;
  background-repeat: no-repeat;
}

%font-additional {
  font-family: $font-family-text;
  font-weight: 400;
  font-size: 14px;
}

%font-main {
  font-family: $font-family-text;
  font-weight: 400;
  font-size: 16px;
}

%font-footer-title {
  font-family: $font-family-title;
  font-weight: 700;
  font-size: 18px;
}

%button-text {
  font-family: $font-family-text;
  font-weight: 600;
  font-size: 16px;
}

%h6 {
  font-family: $font-family-title;
  font-weight: 700;
  font-size: 20px;
}

%h5 {
  font-family: $font-family-title;
  font-weight: 700;
  font-size: 14px;
}

%h4 {
  font-family: $font-family-title;
  font-weight: 700;
  font-size: 20px;

  @include onTablet {
    font-size: 24px;
  }
}

%h3 {
  font-family: $font-family-title;
  font-weight: 700;
  font-size: 28px;

  @include onTablet {
    font-size: 40px;
  }

  @include onDesktop {
    font-size: 48px;
  }
}

%h2 {
  font-family: $font-family-title;
  font-weight: 700;
  font-size: 38px;

  @include onTablet {
    font-size: 62px;
  }

  @include onDesktop {
    font-size: 80px;
  }
}

%h1 {
  font-family: $font-family-title;
  font-weight: 700;
  font-size: 48px;

  @include onTablet {
    font-size: 72px;
  }

  @include onDesktop {
    font-size: 96px;
  }
}

%dd-list-font {
  font-family: $font-family-text;
  font-weight: 500;
  font-size: 16px;
  text-transform: uppercase;
}

%logo-font {
  font-family: $font-family-text;
  font-weight: 700;
  font-size: 36px;
  text-transform: uppercase;
}

%footer-hover-blocks {
  transition: $transition-all;

  &:hover {
    transform: scale(1.04) translateX(6px);
  }
}

%footer-links {
  transition: $transition-all;

  &:visited {
    color: $text-color-white;
  }

  &:hover {
    color: $button-action-color;
    transform: scale(1.03);
  }
}

%subscribe-active-hover-input {
  border-color: $button-action-color;
  color: $button-action-color;

  &::placeholder {
    color: $button-action-color;
    font-weight: 400;
  }
}

%button-hover {
  background-color: $button-hover;
}

%button-pressed {
  background-color: $button-pressed;
}
